import React, { useState } from 'react';
import { Button, ButtonGroup, Table, Row, Col, Form, Modal } from "react-bootstrap";

function Product() {
  const [brandShow, setBrandShow] = useState(false);
  const [productShow, setProductShow] = useState(false);

  const handleBClose = () => setBrandShow(false);
  const handleBShow = () => setBrandShow(true);

  const handlePClose = () => setProductShow(false);
  const handlePShow = () => setProductShow(true);
  

  return (
    <>
       {/* - */}
       <Modal aria-labelledby="contained-modal-title-vcenter" centered show={brandShow} onHide={handleBClose}>
        <Modal.Header closeButton>
          <Modal.Title> ()</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="ModalForm.Brand">
              <Form.Label></Form.Label>
              <Form.Control type="input" placeholder=" " autoFocus />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleBClose}>
            
          </Button>
          <Button variant="primary" onClick={handleBClose}>
            
          </Button>
        </Modal.Footer>
      </Modal>

      {/* - */}
      <Modal aria-labelledby="contained-modal-title-vcenter" centered show={productShow} onHide={handlePClose}>
        <Modal.Header closeButton>
          <Modal.Title> ()</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="ModalForm.Brand">
              <Form.Label></Form.Label>
              <Form.Select aria-label="Default select example" autoFocus>
                <option> </option>
                <option value="1"> </option>
                <option value="2"></option>
                <option value="3"></option>
                <option value="4"></option>
                <option value="5"></option>
                <option value="6"></option>
                <option value="7"></option>
                <option value="8"></option>
                <option value="9"></option>
                <option value="10"></option>
                <option value="11"></option>
              </Form.Select>
            </Form.Group>
            <Form.Group className="mb-3" controlId="ModalForm.Product">
              <Form.Label></Form.Label>
              <Form.Control type="input" placeholder=" " />
            </Form.Group>
            <Form.Group className="mb-3" controlId="ModalForm.Type">
              <Form.Label></Form.Label>
              <Form.Select aria-label="Default select example">
                <option> </option>
                <option value="1"></option>
                <option value="2"></option>
              </Form.Select>
            </Form.Group>
            <Form.Group className="mb-3" controlId="ModalForm.Unit">
              <Form.Label></Form.Label>
              <Form.Select aria-label="Default select example">
                <option> </option>
                <option value="1"></option>
                <option value="2"></option>
              </Form.Select>
            </Form.Group>
            <Form.Group className="mb-3" controlId="ModalForm.Etc">
              <Form.Label></Form.Label>
              <Form.Control type="input" placeholder=" " />
            </Form.Group>
            <Form.Group controlId="formFile" className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control type="file" />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handlePClose}>
            
          </Button>
          <Button variant="primary" onClick={handlePClose}>
            
          </Button>
        </Modal.Footer>
      </Modal>


      <h2 className="title-h2">/</h2>

      <Row className="mt-2">
        <Col xl="5">

          <Row className="mt-4">
            <Col className="me-auto col-auto">
              <h3 className="title-h3"></h3>
            </Col>
            <Col className="col-auto">
              <ButtonGroup aria-label="Basic example">
                <Button variant="primary" onClick={handleBShow}></Button>
                <Button type="reset" value="Reset" variant="outline-primary" className="ms-2"></Button>
              </ButtonGroup>
            </Col>
          </Row>

          <div className="tbl-type1 table-responsive mt-3">
            <Table hover>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td> </td>
                  <td>1</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>1</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>1</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>1</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>1</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td><Button variant="outline-primary" size="sm"></Button></td>
                </tr>
              </tbody>
            </Table>
          </div>
        </Col>

        <Col xl="7">
          <Row className="mt-4">
            <Col className="me-auto col-auto">
              <h3 className="title-h3"></h3>
            </Col>
            <Col className="col-auto">
              <ButtonGroup aria-label="Basic example">
                <Button variant="primary" onClick={handlePShow}></Button>
              </ButtonGroup>
            </Col>
          </Row>

          <div className="tbl-type1 table-responsive mt-3">
            <Table hover>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td> </td>
                  <td>1</td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>1</td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>0</td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>1</td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
              </tbody>
            </Table>
          </div>
        </Col>
      </Row>

    </>
  )
}

export default Product;