import React, { useState, useRef, useEffect } from 'react';
import { Link } from "react-router-dom";
import axios from "axios";
import Editor from '../components/Editor';
import UploadFiles from '../commons/UploadFiles';
// import LFSelect from '../commons/LFSelect';


const BoardWrite = () => {

  // const tabs = [
  //   { value: '', text: '' },
  //   { value: '', text: '' }
  // ]

  const [id, setId] = useState(0);
  const [title, setTitle] = useState('');
  const [desc, setDesc] = useState('');
  // const [type, setType] = useState('');
  const uploadReferenece = React.createRef();


  async function onClickSearch() {
    // if (title.trim() == '') {
    //     alert(' '); return;
    // }
    // if (desc.trim() == '') {
    //     alert(' '); return;
    // }

    await uploadReferenece.current.upload().then(function (result) {
      const files = result;

      axios.post('/api/board/', { title: title, desc: desc, files: files }, (res) => {
          if (res.data && res.data.ok === 1) {
              alert(' ');
              setId(res.data.insertedId);
              console.log(res.data);
              var linkToClick = document.getElementById('notice_Detail_Link');
              linkToClick.click();
          } else {
              alert('    .')
          }
      }, null, true); 

    }).catch(function (err) {
    });
  }

  function onEditorChange(value) {
    setDesc(value)
  }

  return (
    <>
      <h2 className="title-h2"></h2>

      <div className="d-md-flex justify-content-md-end">
        <Link to="/BoardList" className="btn btn-secondary"></Link>
      </div>

      <div style={{ padding: "12px" }}>
        <div className="form-group">
          {/* <LFSelect options={tabs} onChange={(event) => setType(event.target.value)}/> */}
        </div>
        <div className="form-group">
          <input type="text" placeholder="" className="form-control" onChange={(event) => setTitle(event.target.value)} />
        </div>

        <UploadFiles ref={uploadReferenece} />
        <Editor value={desc} onChange={onEditorChange} />

        <div className="text-center pd12">
          <button className="lf-button primary" onClick={onClickSearch}></button>
        </div>
        <Link id="notice_Detail_Link" to={{ pathname: '/BoardDetail', state: { id: id } }}></Link>
      </div>

    </>
  )
}

export default BoardWrite;